import { Component, OnInit } from '@angular/core';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';
import { LoadingController, ModalController, NavController, NavParams, AlertController } from '@ionic/angular';
import { Storage } from '@ionic/storage';
import { Constants, StatusCode, LocalStorageCache, ListItem } from 'src/app/data-model/constant.model';
import { MachineModel } from 'src/app/data-model/base.model';
import { BaseService } from 'src/app/services/base.service';

@Component({
  selector: 'app-sel-machine',
  templateUrl: './sel-machine.component.html',
  styleUrls: ['./sel-machine.component.scss'],
})
export class SelMachineComponent extends BasePagePage implements OnInit {

  cacheMachine: LocalStorageCache = new LocalStorageCache();

  // arrSelectedProcess: Array<any> = [];
  // arrMachine: Array<any> = [];
  machineReqModel: MachineModel = new MachineModel();
  // machine: any;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private modalCtrl: ModalController,
    public navCtrl: NavController,
    private navParams: NavParams,
    private service: BaseService,
    private storage: Storage) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {
    if (this.navParams && this.navParams.data && this.navParams.data.arrSelectedProcess && this.navParams.data.arrSelectedProcess.length > 0) {
      this.callWSGetMachine();
    }
  }

  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss(this.cacheMachine.selected);
  }

  callWSGetMachine() {

    this.machineReqModel.process = this.navParams.data.arrSelectedProcess;

    this.storage.get(Constants.c_OP_MACHINE).then((machine: LocalStorageCache) => {

      if (machine && machine.list && machine.list.length > 0) {

        this.cacheMachine.list = machine.list;

      } else {

        this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

          this.machineReqModel.accesstoken = accessToken;

          this.service.presentLoading();

          this.service.callWebService(Constants.k_GET_MACHINE, this.machineReqModel, (res) => {

            this.service.dismissLoading();

            if ((res && res.data && res.data != '' && res.data != '-') && res.status_code == StatusCode.Success) {

              this.cacheMachine.list = JSON.parse(res.data);

            } else {

              this.storage.remove(Constants.c_OP_PROCESS);

              this.cancelModal();

            }

          });

        });

      }

    });

  }

  onMachineClicked(machine: ListItem) {

    this.cacheMachine.selected = machine;

    // CACHE FOR MACHINE SELECTION IF USER ENTER FOR SECOND TIME AFTER CLOSE / KILL APP
    this.storage.set(Constants.c_OP_MACHINE, this.cacheMachine)

    this.closeModal();

  }

}
