  async cancelModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss();
  }

  async closeModal() {
    // const onClosedData: string = "Wrapped Up!";
    await this.modalCtrl.dismiss(this.cacheProcess);
  }

  callWSGetProcess() {

    this.processReqModel.dept = this.navParams.data.arrSelectedDept;

    this.storage.get(Constants.c_OP_PROCESS).then((process: LocalStorageCache) => {

      if (process && process.list && process.list.length > 0) {

        this.cacheProcess.list = process.list;

      } else {

        // GET LATEST PROCESS LIST
        this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

          this.processReqModel.accesstoken = accessToken;

          this.service.presentLoading();

          this.service.callWebService(Constants.k_GET_PROCESS, this.processReqModel, (res) => {

            if ((res && res.data && res.data != '' && res.data != '-') && res.status_code == StatusCode.Success) {

              this.cacheProcess.list = JSON.parse(res.data);

            }

            this.service.dismissLoading();

          });

        });

      }

    });

  }

