  callWSGetDept() {

    this.storage.get(Constants.c_OP_DEPARTMENT).then((department) => {

      if (department == undefined || department == null) {

        // GET LATEST DEPARTMENT LIST
        this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

          this.deptReqModel.accesstoken = accessToken;

          this.service.presentLoading();

          this.service.callWebService(Constants.k_GET_DEPT, this.deptReqModel, (res) => {

            if ((res && res.data && res.data != '' && res.data != '-') && res.status_code == StatusCode.Success) {

              this.cacheDept.list = JSON.parse(res.data);

            }

            this.service.dismissLoading();

          });

        });

      } else {

        this.cacheDept.list = department.list;

      }

    });

  }

