import { Constants, StatusCode, Dictionary, LocalStorageCache, ListItem } from '../data-model/constant.model';
import { Storage } from '@ionic/storage';
import { BasePagePage } from '../shared/base-page/base-page.page';
import { ModalBaseComponent } from '../modal-base/modal-base.component';
import { SelDeptComponent } from './sel-dept/sel-dept.component';
import { Observable, forkJoin, Subscription, timer } from 'rxjs';
import { SelMachineComponent } from './sel-machine/sel-machine.component';
import { MatListItem } from '@angular/material/list';
import { SelProcessComponent } from './sel-process/sel-process.component';

@Component({
  selector: 'app-operator',
  templateUrl: './operator.page.html',
  styleUrls: ['./operator.page.scss'],
})
export class OperatorPage extends BasePagePage implements OnInit, OnDestroy {

  public statusColor: string = 'none';
  public statusDown: string = "#E62C04";
  public statusRunning: string = "#30FA14";
  public statusIdle: string = "#FAF714";

  strMachineName: string = '';
  barcodeData: any;
  selectedMachine: string = '';
  arrRow: Array<any> = [];
  isOk: boolean = false;
  isMachineFound: boolean = false;
  isIdle: boolean = false;
  countDown: Subscription;
  isFlash: boolean = false;;
  tick = 500;
  isButtonDisabled: boolean = false;

  transfileReqModel: TransfileModel = new TransfileModel();
  arrTransfile: Array<any> = [];
  issueMachineDownReqModel: IssueMDownModel = new IssueMDownModel();
  arrMachineDown: Array<any> = [];

  smsReqModel: SMSModel = new SMSModel();

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService,
    private storage: Storage,
    public modalCtrl: ModalController,
    private barcodeScanner: BarcodeScanner
  ) {

    super(loadingCtrl, alertCtrl);

  }

  ngOnInit() {

    this.countDown = timer(0, this.tick).subscribe(() => {

      this.isFlash = !this.isFlash

    });

  }

  ionViewWillEnter() {

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.transfileReqModel.accesstoken = accessToken;

      this.issueMachineDownReqModel.accesstoken = accessToken;

      this.smsReqModel.accesstoken = accessToken;

      this.callWSToGetPreselectionData();

    });

  }

  // ionViewDidEnter() {

  //   this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

  //     let deptReqModel: DeptModel = new AccessTokenModel();
  //     deptReqModel.accesstoken = accessToken;
  //     let ws = new WebService(Constants.k_GET_DEPT, deptReqModel, (res) => {
  //       console.log('callback only');
  //     });

  //     let deviceReqModel: DeviceModel = new AccessTokenModel();
  //     deviceReqModel.accesstoken = accessToken;
  //     let ws1 = new WebService(Constants.k_GET_DEVICE, deviceReqModel, (res) => {
  //       console.log('callback only111');
  //     });

  //     let machineDownReasonReqModel: DownModel = new AccessTokenModel();
  //     machineDownReasonReqModel.accesstoken = accessToken;
  //     let ws2 = new WebService('TPInsight_GetMachineDownReason', machineDownReasonReqModel, (res) => {
  //       console.log('callback only222');
  //     });

  //     this.service.callWebServiceSimultaneously([ws, ws1, ws2], (res)=> {
  //       console.log('done yeah');
  //     }, true);

  //   });

  // }

  ngOnDestroy() {

    this.countDown = null;

  }

  callWSToGetPreselectionData() {

    this.storage.get(Constants.c_OP_MACHINE).then((machine: LocalStorageCache) => {

      if (machine && machine.selected && machine.selected.name && machine.selected.value) {

        this.strMachineName = machine.selected.name;

        this.transfileReqModel.machineid = machine.selected.value.toString();

        this.arrTransfile = [];

        this.service.presentLoading();

        this.service.callWebService(Constants.k_GET_TRANSFILE, this.transfileReqModel, (res) => {

          this.service.dismissLoading();

          this.afterGetTransfile(res);

          if (this.arrTransfile && this.arrTransfile.length > 0 && this.arrTransfile[0].Status) {

            let strMachineStatus = String(this.arrTransfile[0].Status)

            if (strMachineStatus.toLowerCase() == 'down') {

              BasePagePage.presentAlert('Alert', 'Machine ' + this.arrTransfile[0].Machine + ' is in ' + strMachineStatus + ' State. Do you want to continue to machine down detail page?', (res) => {

                if (res.role == Constants.b_OK) {

                  this.issueMachineDownReqModel.transfileID = this.arrTransfile[0].TransfileID;

                  this.arrMachineDown = [];

                  this.service.presentLoading();

                  this.service.callWebService(Constants.k_GET_ISSUE_MDOWN, this.issueMachineDownReqModel, (res) => {

                    this.service.dismissLoading();

                    if (res.status_code == StatusCode.Success) {

                      this.arrMachineDown = JSON.parse(res.data);

                      this.service.setData('downstart', {
                        'machineDown': this.arrMachineDown[0],
                        'sms': this.smsReqModel,
                        'transfile': this.arrTransfile[0]
                      });

                      this.navCtrl.navigateForward('/operator/machine-down/downstart')

                    }

                  });

                }

              }, [Constants.b_CANCEL, Constants.b_OK]);

            }

          }

        });

      }

    });

  }

  closeMenu() {

    this.barcodeScanner.scan().then(barcodeData => {
      this.barcodeData = barcodeData;
      console.log('Barcode data', this.barcodeData);
    }).catch(err => {
      console.log('Error', err);
    });

  }

  onButtonClicked(e: Event) {
    console.log('onButtonClicked: ', e.srcElement['innerText']);
  }

  resetField() {
    this.isMachineFound = false;
    this.isIdle = false;
    this.strMachineName = '';
    this.arrTransfile = [];
    this.storage.remove(Constants.c_OP_SEL_DEPT);
    this.storage.remove(Constants.c_OP_SEL_PROC);
    this.storage.remove(Constants.c_OP_SEL_MACH);
    this.storage.remove(Constants.c_OP_DEPARTMENT);
    this.storage.remove(Constants.c_OP_PROCESS);
    this.storage.remove(Constants.c_OP_MACHINE);
  }

  onResetClicked() {

    BasePagePage.presentAlert('Alert', 'Are you sure want to reset?', (res) => {

      if (res.role = Constants.b_OK) {

        this.resetField();

      }

    }, [Constants.b_CANCEL, Constants.b_OK]);

  }

  numberOnlyValidation(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);

    if (!pattern.test(inputChar)) {
      // invalid character, prevent input
      event.preventDefault();
    }
  }

  onMachineSelected() {
    console.log('selectedMachine: ', this.selectedMachine);
  }

  onSubmit(form) {

    console.log('datatatat: ', form.value);

    if (this.isMachineFound == true) {
      this.navCtrl.navigateForward('/operator/machine-down');
    } else {
      this.isMachineFound = true;
    }

  }

  onOptionClicked() {

    this.storage.get(Constants.c_OP_PROCESS).then((process: LocalStorageCache) => {

      if (process && process.selected && process.list && process.list.length > 0) {

        this.presentModal(Constants.c_OP_PROCESS, process);
        
      } else {

        this.storage.get(Constants.c_OP_DEPARTMENT).then((dept: LocalStorageCache) => {

          if (dept && dept.selected && dept.list && dept.list.length > 0) {
        
            this.presentModal(Constants.c_OP_DEPARTMENT, dept);
            
          } else {
    
            this.presentModal();
            
          }
    
        });
        
      }

    });

  }

  async presentModal(key?: string, item?: LocalStorageCache) {

    var component: Object;
    this.isButtonDisabled = true;

    if (key && item && item.selected && item.list && item.list.length > 0) {
      component = (key == Constants.c_OP_PROCESS) ? { rootPage: SelMachineComponent, rootPageParams: { 'arrSelectedProcess': [item.selected.value] } } : { rootPage: SelProcessComponent, rootPageParams: { 'arrSelectedDept': [item.selected.value] } };
    } else {
      component = { rootPage: SelDeptComponent };
    }

    const modal = await this.modalCtrl.create({
      component: ModalBaseComponent,
      componentProps: component
    });

    modal.onDidDismiss().then((dataReturned) => {

      this.isButtonDisabled = false;

      if (dataReturned && dataReturned.data && dataReturned.data != undefined) {

        const selected: ListItem = Object.assign({}, dataReturned.data);

        this.transfileReqModel.machineid = selected.value.toString();

        this.smsReqModel.machineid = selected.value.toString();

        this.strMachineName = selected.name.toString();

        this.arrTransfile = [];

        this.service.presentLoading();

        this.service.callWebService(Constants.k_GET_TRANSFILE, this.transfileReqModel, (res) => {

          this.service.dismissLoading();

          this.afterGetTransfile(res);

        });

      }

    });

    return await modal.present();

  }

  readable(val) {
    return val.toString().match(/\d/g).join("");
  }

  onSearchJobClicked() {

    this.transfileReqModel.machineid = this.strMachineName;

    this.arrTransfile = [];

    this.service.presentLoading();

    this.service.callWebService(Constants.k_GET_TRANSFILE, this.transfileReqModel, (res) => {

      this.service.dismissLoading();

      this.afterGetTransfile(res);

    });

  }

  afterGetTransfile(response: any) {

    this.isMachineFound = (response && response.status_code && (response.status_code == StatusCode.Success));

    this.isIdle = (response && response.status_code && (response.status_code != StatusCode.Success) && (response.data && response.data != '-') && response.data.includes('Idle State'));

    if (this.isMachineFound) {

      this.arrTransfile = JSON.parse(response.data);

      this.strMachineName = this.arrTransfile[0].Machine;

      this.smsReqModel.machineid = this.transfileReqModel.machineid;

      this.smsReqModel.processcode = this.arrTransfile[0].ProcessCode;

      if (String(this.arrTransfile[0].Status).toLowerCase().includes('running')) {
        this.statusColor = this.statusRunning;
      } else if (String(this.arrTransfile[0].Status).toLowerCase().includes('down')) {
        this.statusColor = this.statusDown;
      }

      var cache: LocalStorageCache = new LocalStorageCache();
      cache.selected = {
        name: this.arrTransfile[0].Machine,
        value: this.transfileReqModel.machineid
      }

      this.storage.get(Constants.c_OP_MACHINE).then((machine: LocalStorageCache) => {

        if (machine && machine.list && machine.list.length > 0) {

          cache.list = machine.list;

        } else {

          cache.list = [];

        }

        this.storage.set(Constants.c_OP_MACHINE, cache);

      });

    } else {

      if (this.isIdle) {

        let strRawMsg = String(response.data);

        this.strMachineName = strRawMsg.replace('Machine ', '').replace(' is in Idle State', '');

      } else {

        this.resetField();

      }

    }

  }

  keyboardChanged() {

    if (this.strMachineName == "") {
      this.resetField();
    }

  }

  onMachineDownClicked() {

    if (this.statusColor == this.statusDown) {

      this.issueMachineDown();

    } else {

      BasePagePage.presentAlert('Alert', 'Are you sure want to issue down this machine?<br/><br/>' + this.arrTransfile[0].Machine, (res) => {

        if (res.role = Constants.b_OK) {

          this.issueMachineDown();

        }

      }, [Constants.b_CANCEL, Constants.b_OK]);

    }

  }

  issueMachineDown() {

    this.issueMachineDownReqModel.transfileID = this.arrTransfile[0].TransfileID;

    this.arrMachineDown = [];

    this.service.presentLoading();

    this.service.callWebService(Constants.k_GET_ISSUE_MDOWN, this.issueMachineDownReqModel, (res) => {

      this.service.dismissLoading();

      if (res.status_code == StatusCode.Success) {

        this.arrMachineDown = JSON.parse(res.data);

        this.arrTransfile = [];

        this.service.presentLoading();

        this.service.callWebService(Constants.k_GET_TRANSFILE, this.transfileReqModel, (res) => {

          this.service.dismissLoading();

          this.afterGetTransfile(res);

          this.service.setData('downstart', {
            'machineDown': this.arrMachineDown[0],
            'sms': this.smsReqModel,
            'transfile': this.arrTransfile[0]
          });

          this.navCtrl.navigateForward('/operator/machine-down/downstart')

        });

      }

    });

  }

}
